import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
    children: [
      {
        path: "",
        component: () => import("../views/HomepageView.vue"),
      },
      {
        path: "/shoppingmall",
        component: () => import("../views/ShoppingmallView.vue"),
      },
      {
        path: "/livestreaming",
        component: () => import("../views/LivestreamingView.vue"),
      },
      {
        path: "/me",
        component: () => import("../views/MeView.vue"),
      },
    ],
  },
  {
    path: "/classify",
    component: () => import("../views/ClassifyView.vue"),
  },
  {
    path: "/searchresult",
    component: () => import("../views/SearchResult.vue"),
  },
  {
    path: "/commodity",
    component: () => import("../views/CommodityView.vue"),
  },
  {
    path: "/shopcart",
    component: () => import("../views/ShopcartView.vue"),
  },
  {
    path: "/liveroom",
    component: () => import("../views/LiveRoom.vue"),
  },
  {
    path: "/playback",
    component: () => import("../views/PlayBack.vue"),
  },
  {
    path: "/confirmorder",
    name:"confirmorder",
    component: () => import("../views/ConfirmAnOrder.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
