import Vue from "vue";
import Vuex from "vuex";
// import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    homenav: [],
    homelist: [],
    shoppingmallnav: [],
    shoppingmallLike: [],
    livetabheader: [],
    liveroom: [],
    classifyMainNav: [],
    classifySecondNav: [],
    classifyMiniwall: null,
    searchResult: null,
    searchHot: null,
    commodityDdetails: null,
    PromotionList: null,

    cartlist: JSON.parse(localStorage.getItem("cartlist")) || [],
  },
  getters: {},
  mutations: {
    getcartlist: function (
      state,
      { nums, title, price, size = "", color = "", src, id }
    ) {
      state.cartlist = [
        ...new Set([
          {
            nums,
            title,
            price,
            size,
            color,
            src,
            isSelected: true,
            id,
          },
          ...state.cartlist,
        ]),
      ];

      console.log({ nums, title, price, size, color, src, id });
    },

    toggleTodo: function (state, { id, isSelected }) {
      state.cartlist = state.cartlist.map((o) =>
        o.id === id ? { ...o, isSelected } : o
      );
    },

    checkedAllTodo: function (state, check) {
      state.cartlist = state.cartlist.map((o) => ({ ...o, isSelected: check }));
    },

    deleteTodo: function (state) {
      state.cartlist = state.cartlist.filter((o) => o.isSelected === false);
    },

    addnum: function (state, { id, nums }) {
      state.cartlist = state.cartlist.filter((o) =>
        o.id === id ? { ...o, nums } : o
      );
    },

    subtractnum: function (state, { id, nums }) {
      state.cartlist = state.cartlist.filter((o) =>
        o.id === id ? { ...o, nums } : o
      );
    },

    homenav: function (state, data) {
      state.homenav = data;
    },
    homelist: function (state, data) {
      state.homelist = data;
    },
    shoppingmallnav: function (state, data) {
      state.shoppingmallnav = data;
    },
    shoppingmallLike: function (state, data) {
      state.shoppingmallLike = data;
    },
    livetabheader: function (state, data) {
      state.livetabheader = data;
    },
    liveroom: function (state, data) {
      state.liveroom = data;
    },
    classifyMainNav: function (state, data) {
      state.classifyMainNav = data;
    },
    classifySecondNav: function (state, data) {
      state.classifySecondNav = data;
    },
    classifyMiniwall: function (state, data) {
      state.classifyMiniwall = data;
    },
    searchResult: function (state, data) {
      state.searchResult = data;
    },
    searchHot: function (state, data) {
      state.searchHot = data;
    },

    commodityDdetails: function (state, data) {
      state.commodityDdetails = data;
    },
    PromotionList: function (state, data) {
      state.PromotionList = data;
    },
  },
  actions: {
    getHomenav: function (context) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/homeNav.json")
        .then((res) => {
          context.commit("homenav", res.data);
          console.log(res);
        });
    },

    getHomelist: function (context, page) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/video/item", {
          params: {
            page: page,
          },
        })
        .then((res) => {
          context.commit("homelist", res.data.data.list);
        });
    },
    getShoppingmallnav: function (context) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/shopNav.json")
        .then((res) => {
          context.commit("shoppingmallnav", res.data);
        });
    },

    getShoppingmallLike: function (context, page) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/goods/personalized", {
          params: {
            page: page,
          },
        })
        .then((res) => {
          context.commit("shoppingmallLike", res.data.data.wall.docs);
        });
    },

    getLiveTabheader: function (context) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/live/tabHeader")
        .then((res) => {
          context.commit("livetabheader", res.data.data.tabs.items);
        });
    },
    getLiveRoom: function (context, tabid) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/live/tabList", {
          params: {
            tabId: tabid,
            pageIndex: 1,
          },
        })
        .then((res) => {
          context.commit("liveroom", res.data.data.lives);
        });
    },

    getClassifyMainNav: function (context) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/category.json")
        .then((res) => {
          context.commit("classifyMainNav", res.data.data[117330].list);
        });
    },
    getClassifySecondNav: function (context, pid) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/category/getSub", {
          params: {
            pid,
          },
        })
        .then((res) => {
          context.commit("classifySecondNav", res.data.data.list);
        });
    },

    getSearchResult: function (context, { q, page, sort, offset }) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/search/searchByKeyword", {
          params: {
            q,
            page,
            sort,
            offset,
          },
        })
        .then((res) => {
          context.commit("searchResult", res.data.result);
        });
    },

    getsearchHot: function (context) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/hotSearch.json")
        .then((res) => {
          context.commit("searchHot", res.data.data);
        });
    },

    getcommodityDdetails: function (context, itemId) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/goods/detail", {
          params: {
            itemId,
          },
        })
        .then((res) => {
          context.commit("commodityDdetails", res.data.data.result);
        });
    },
    getPromotionList: function (context, itemId) {
      Vue.axios
        .get("https://apis.netstart.cn/mogujie/goods/getPromotionList", {
          params: {
            itemId,
          },
        })
        .then((res) => {
          context.commit("PromotionList", res.data.data.flushKey.data);
        });
    },
  },
  modules: {},
});
